import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import Training1 from './views/Training1.vue'
import Training2 from './views/Training2.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
        path: '/training-1',
        name: 'training1',
        component: Training1
    },
    {
        path: '/training-2',
        name: 'training2',
        component: Training2
    }
  ]
})
